import { faBolt } from "@fortawesome/free-solid-svg-icons"
import { faIndustry } from "@fortawesome/free-solid-svg-icons";
import { faGraduationCap } from "@fortawesome/free-solid-svg-icons";
import { faCoins } from "@fortawesome/free-solid-svg-icons";
import { faLightbulb } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import {Row, Col, Container} from 'react-bootstrap';
import './Miertmi.css';

export const Miertmi = () => {
    return(
        <section class='miertmi' id='miertmi'>
            <Container className='miertmi-container'>
                <Row>
                    <h1>Mirt a TzTrTech?</h1>
                    <Col xs={12} md={4} lg={4}>
                        <Container className="card">
                            <FontAwesomeIcon className='ikon' icon={faBolt} size="3x"/>
                            <h3>Villmgyors reagls</h3>
                            <p className="miert-p">Tudjuk, mekkora veszlyt s kltsget jelenthet egy ll get, ezrt mindent megtesznk, hogy minl hamarabb mkdkpes legyen a berendezs.</p>
                        </Container>
                    </Col>
                    <Col xs={12} md={4} lg={4}>
                        <Container className="card">
                            <FontAwesomeIcon className='ikon' icon={faIndustry} size="3x"/>
                            <h3>Helyszni megoldsok</h3>
                            <p className="miert-p">Nlunk nincs szksg a kszlk sztszerelsre s elszlltsra. A gondot ltalban helyben, egyetlen kiszllssal orvosoljuk.</p>
                        </Container>
                    </Col>
                    <Col xs={12} md={4} lg={4}>
                        <Container className="card">
                            <FontAwesomeIcon className='ikon' icon={faGraduationCap} size="3x"/>
                            <h3>Magas szint szakrtelem</h3>
                            <p className="miert-p">Tbb mint hsz ve foglalkozunk tzterekkel, klnsen az Addfield s Vulcan getkkel. Tapasztalatunkra s egyedi fejleszts megoldsainkra mindig szmthat.</p>
                        </Container>
                    </Col>
                </Row>
                <Row>
                <Col xs={0} md={2} lg={2}>
                </Col>
                    <Col xs={12} md={4} lg={4}>
                        <Container className="card">
                            <FontAwesomeIcon className='ikon' icon={faCoins} size="3x"/>
                            <h3>Kltsghatkonysg</h3>
                            <p className="miert-p">Azltal, hogy az egsz folyamatot az n telephelyn vgezzk, rengeteg idt s pnzt sprolunk meg.</p>
                        </Container>
                    </Col>
                    <Col xs={12} md={4} lg={4}>
                        <Container className="card">
                            <FontAwesomeIcon className='ikon' icon={faLightbulb} size="3x"/>
                            <h3>Innovci s fejleszts</h3>
                            <p className="miert-p">Folyamatosan tesztelnk j anyagokat, alkatrszeket s javtsi megoldsokat, hogy a berendezsek lettartamt s hatkonysgt mg tovbb nvelhessk.</p>
                        </Container>
                    </Col>
                </Row>
            </Container>
        </section>
    )
}