import {Container} from 'react-bootstrap';
import './bemutatkozas.css';

export const Bemutatkozas = () => {
    return(
        <section className='bemutatkozas' id='bemutatkozas'>
            <Container>
                <br></br>
                <br></br>
                <h1>Bemutatkozs</h1>
                <p className="udvozles">dvzljk a TzTrTech vilgban, ahol a szakrtelem s az innovci tallkozik, hogy a lehet legtbbet hozhassa ki getberendezseibl!</p>
                <br></br>
                <p className='bemut'>Tbb mint kt vtizede dolgozunk azon, hogy gyfeleink telephelyein a tzterek ne csak mkdjenek, hanem hossz tvon is maximlis hatkonysgot s zembiztonsgot nyjtsanak. Ez id alatt szmos ipari ltestmnynek segtettnk talaktani, modernizlni vagy pp feljtani az getrendszereit - legyen sz <span className='kiemel'>Addfield, Vulcan</span> vagy ms tpusokrl.</p>
                <br></br>
                <p className='bemut'>Ami igazn megklnbztet minket, az a <span className='kiemel'>gyakorlati tapasztalat</span> s az lland <span className='kiemel'>fejlesztsi szemllet</span>. Olyan egyedi megoldsokat dolgoztunk ki, amelyekkel cskkenthet a meghibsodsok kockzata, mikzben jelentsen nvelhet a berendezsek lettartama. Nem csupn gyorsan reaglunk, de <span className='kiemel'>helyben</span> javtunk, gy nnek nem kell drga s idignyes szlltssal bajldnia.</p>
                <br></br>
                <p className='bemut'><span className='kiemel'>Clunk egyszer</span>: hossz tv partnerei szeretnnk lenni minden olyan vllalkozsnak, amely kiemelt figyelmet fordt a mkds biztonsgra s a krnyezet vdelmre. Bzza rnk getit, s tapasztalja meg, milyen, amikor egy professzionlis csapat vllal felelssget az n berendezseinek zavartalan mkdsrt!</p>
            </Container>
        </section>
    );
}