import { Container, Row, Col } from "react-bootstrap";
import './Szolgaltatasaink.css';
import {faBrain} from '@fortawesome/free-solid-svg-icons';
import { faScaleBalanced } from "@fortawesome/free-solid-svg-icons";
import { faHandFist } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";


export const Szolgaltatasaink = () => {
  return (
    <section className="szolgaltatasaink" id="szolgaltatasaink">
        <Container>
        <h1>Szolgltatsaink</h1>
        <h2>Javts s feljts</h2>
          <Row className='align-items-center'>
            <Col xs={12} md={6} xl={6}>
              <ul>
                <li className='list-item'>
                  <FontAwesomeIcon className='ikon' icon={faBrain} size="3x"/>
                  <div className='text-content'>
                    <h2>Helyszni diagnosztika</h2>
                    <p>Els lpsben alaposan felmrjk az get aktulis llapott: vizsgljuk a bels falazatot, a szigetelst, az gfejet, valamint az elektronikai s mechanikai elemeket.</p>
                  </div>
                </li>
                <li className='list-item'>
                <FontAwesomeIcon className='ikon balance' icon={faScaleBalanced} size="2x"/>
                  <div className='text-content'>
                    <h2>Clzott beavatkozs</h2>
                    <p>A meghibsodott vagy elavult alkatrszeket a helysznen cserljk, javtjuk. Nem kell a kszlket leszerelni s mshov szlltani, ezzel idt s kltsget takartunk meg.</p>
                  </div>
                </li>
                <li className='list-item'>
                  <FontAwesomeIcon className='ikon' icon={faHandFist} size="3x"/>
                  <div className='text-content'> 
                    <h2>Modernizls, hatkonysgnvels</h2>
                    <p>Sajt tapasztalataink s fejlesztseink rvn olyan apr mdostsokat vgznk, amelyek nvelik az gs hatsfokt, gy cskkentve az zemeltetsi kltsgeket.</p>
                  </div>
                </li>
              </ul>
            </Col>
            <Col xs={12} md={6} xl={6} className='kep-col'>
            </Col>
          </Row>
        </Container>
    </section>
  )
}
