import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import egeto1 from '../assets/img/egeto1.jpeg';
import egeto2 from '../assets/img/egeto2.jpeg';
import egeto3 from '../assets/img/egeto3.jpeg';
import egeto_utana1 from '../assets/img/egeto_utana1.jpg';
import egeto_utana2 from '../assets/img/egeto_utana2.jpg';
import egeto_utana3 from '../assets/img/egeto_utana3.jpg';
import './tab_pane.css';

const ImageSlider = ({ slides }) => {
  const [currentIndex, setCurrentIndex] = useState(0);

  const goToPrevious = () => {
    const isFirstSlide = currentIndex === 0;
    const newIndex = isFirstSlide ? slides.length - 1 : currentIndex - 1;
    setCurrentIndex(newIndex);
  };

  const goToNext = () => {
    const isLastSlide = currentIndex === slides.length - 1;
    const newIndex = isLastSlide ? 0 : currentIndex + 1;
    setCurrentIndex(newIndex);
  };

  const goToSlide = (slideIndex) => {
    setCurrentIndex(slideIndex);
  };

  // Optional auto-play feature
  useEffect(() => {
    if (slides.length > 1) {
      const timer = setInterval(goToNext, 5000);
      return () => clearInterval(timer);
    }
  }, [currentIndex]);

  return (
    <div style={{ 
      position: 'relative',
      width: '100%',
      height: '400px', // Fixed height
      margin: '20px 0', // Vertical spacing
      borderRadius: '8px',
      overflow: 'hidden' // Contain children
    }}>
      {/* Slider content */}
      <div style={{
        width: '100%',
        height: '100%',
        backgroundImage: `url(${slides[currentIndex]?.imageUrl})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center'
      }} />

      {/* Navigation Arrows */}
      <button
        onClick={goToPrevious}
        style={{
          position: 'absolute',
          top: '50%',
          left: '20px',
          transform: 'translateY(-50%)',
          zIndex: 2,
          background: 'rgba(0,0,0,0.5)',
          border: 'none',
          color: 'white',
          padding: '10px',
          borderRadius: '50%',
          cursor: 'pointer'
        }}
      >
        
      </button>

      <button
        onClick={goToNext}
        style={{
          position: 'absolute',
          top: '50%',
          right: '20px',
          transform: 'translateY(-50%)',
          zIndex: 2,
          background: 'rgba(0,0,0,0.5)',
          border: 'none',
          color: 'white',
          padding: '10px',
          borderRadius: '50%',
          cursor: 'pointer'
        }}
      >
        
      </button>

      {/* Dots Indicator */}
      <div style={{
        position: 'absolute',
        bottom: '20px',
        left: '50%',
        transform: 'translateX(-50%)',
        display: 'flex',
        gap: '10px'
      }}>
        {slides.map((_, index) => (
          <div
            key={index}
            onClick={() => setCurrentIndex(index)}
            style={{
              width: '12px',
              height: '12px',
              borderRadius: '50%',
              background: currentIndex === index ? '#007bff' : 'rgba(255,255,255,0.5)',
              cursor: 'pointer'
            }}
          />
        ))}
      </div>
    </div>
  );
};

export default ImageSlider;