import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import contactImg from "../assets/img/contact-img.svg";
import 'animate.css';
import TrackVisibility from 'react-on-screen';
import axios from 'axios';
import './Hibabejelentes.css';
import { FajlFejltolt } from "./fajltoltesform";
import { useForm } from 'react-hook-form';
import ReCAPTCHA from "react-google-recaptcha";

export const Hibajelentes = () => {
  
  const { register, formState: { errors } } = useForm();
  const [egyszeruData, setEgyszeruData] = useState({
    teljesNev: '',
    email: '',
    telefonszam: '',
    hibaleiras: '',
  });
  const [captchaValue, setCaptchaValue] = useState(null);
  const [images, setImages] = useState([]);  
  const [success, setSuccess] = useState(null);
  const [error, setError] = useState(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [buttonText, setButtonText] = useState('Klds');
  const [status, setStatus] = useState({});
  const todayDate = new Date().toISOString().split("T")[0];

  const [submissionData, setSubmissionData] = useState({
    count: 0,
    lastSubmittedDate: null
  });

  

  // Get submission data from localStorage on component mount
  useEffect(() => {
    const data = JSON.parse(localStorage.getItem("formSubmissionData"));
    if (data) {
      setSubmissionData(data);
    }
  }, []);

  // Reset submission count if it's a new day
  useEffect(() => {
    if (submissionData.lastSubmittedDate !== todayDate) {
      saveSubmissionData(0, todayDate); // Reset submission count for the new day
    }
  }, [submissionData.lastSubmittedDate]);

  // Check if the user has already submitted 3 times today
  useEffect(() => {
    if (submissionData.count >= 3) {
      setError("Naponta csak 3. kldhet hibt!");
      setButtonText("Kikapcsolva");
    }
  }, [submissionData.count]);

  // Save submission data to localStorage
  const saveSubmissionData = (count, date) => {
    const data = { count, lastSubmittedDate: date };
    localStorage.setItem("formSubmissionData", JSON.stringify(data));
    setSubmissionData({ count, lastSubmittedDate: date });
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setEgyszeruData({ ...egyszeruData, [name]: value });
  };

  const onImageChange = (newImages) => {
    setImages(newImages);
  };

  const handleCaptchaChange = (value) => {
    setCaptchaValue(value);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (isSubmitting || submissionData.count >= 3) return;

    setIsSubmitting(true);

    // Check if the user has reached the submission limit
    if (submissionData.count >= 3) {
      setError("You have already submitted 3 times today. Please try again tomorrow.");
      setIsSubmitting(false);
      return; // Prevent further submission
    }

    if(!captchaValue) {
      alert("Krem erstse meg a reCAPTCHAT!");
      return;
    }

    console.log("Submitting form with data:", JSON.stringify(egyszeruData));

    const formAdat = new FormData();
    formAdat.append('teljesNev', egyszeruData.teljesNev);
    formAdat.append('email', egyszeruData.email);
    formAdat.append('telefonszam', egyszeruData.telefonszam);
    formAdat.append('hibaleiras', egyszeruData.hibaleiras);

    images.forEach((image) => {
      formAdat.append('csatoltkepek', image.file);
    });

    try {
      setButtonText('Klds...');
      await axios.post('http://localhost:3001/hibajelentes', formAdat, {
        headers: { 'Content-Type': 'multipart/form-data' },
      });

      setButtonText('Elkldve');
      
      // Increase submission count after successful submission
      saveSubmissionData(submissionData.count + 1, todayDate);
      setSuccess("");

    } catch (error) {
      alert(`Error sending email! ${error}`);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <section className="contact" id="hibabejelentes">
      <Container>
        <Row className="align-items-center">
          <Col size={12} md={6}>
            
                <img  src={contactImg} alt="Contact Us"/>
              
          
          </Col>
          <Col size={12} md={6}>
            <h2>Hibabejelents</h2>
            <form onSubmit={handleSubmit}>
  <Row>
    <Col size={12} sm={6} className="px-1">
      <label>
        Teljes Nv <span style={{ color: 'red' }}>*</span>
      </label>
      <input
        {...register("teljesNev", { required: "Nv megadsa ktelez!" })}
        type="text"
        name="teljesNev"
        value={egyszeruData.teljesNev}
        placeholder="Teljes Nv"
        onChange={handleChange}
      />
      {errors.teljesNev && <span>{errors.teljesNev.message}</span>}
    </Col>

    <Col size={12} sm={6} className="px-1">
      <label>
        E-mail cm <span style={{ color: 'red' }}>*</span>
      </label>
      <input
        {...register("email", { required: "E-mail cm megadsa ktelez!" })}
        type="email"
        name="email"
        value={egyszeruData.email}
        placeholder="E-mail cm"
        onChange={handleChange}
      />
      {errors.email && <span>{errors.email.message}</span>}
    </Col>

    <Col size={12} sm={6} md={12} xl={12} className="px-1">
      <label>
        Telefonszm <span style={{ color: 'red' }}>*</span>
      </label>
      <input
        {...register("telefonszam", { required: "Telefonszm megadsa ktelez!" })}
        type="tel"
        name="telefonszam"
        value={egyszeruData.telefonszam}
        placeholder="Telefonszm"
        onChange={handleChange}
      />
      {errors.telefonszam && <span>{errors.telefonszam.message}</span>}
    </Col>

    <Col size={12} xs={12} className="px-1">
      <label>
        Hibalers <span style={{ color: 'red' }}>*</span>
      </label>
      <textarea
        {...register("hibaleiras", { required: "A hiba rszleteinek megadsa ktelez!" })}
        rows="8"
        name="hibaleiras"
        value={egyszeruData.hibaleiras}
        placeholder="Hibalers"
        onChange={handleChange}
      />
      {errors.hibaleiras && <span>{errors.hibaleiras.message}</span>}
    </Col>

    <Col size={12} md={12} sm={6} xl={12} className="px-1">
      <FajlFejltolt onImageChange={onImageChange} />
    </Col>
    <Col size={12} md={12} sm={12} xl={12} className="px-1">
      <ReCAPTCHA sitekey="6LcvSMwqAAAAAGtqTpancMkWNs0zW8TF4sDGSU4d" onChange={handleCaptchaChange}/>
      <br></br>
    </Col>
    <Col size={12} className="px1">
      <div className="kuldes">
        <button onClick={() => setButtonText('Klds...')} className='kuldes-gomb' type="submit" disabled={isSubmitting}>
          <span>{buttonText}</span>
        </button>
      </div>
    </Col>

    {status.message && (
      <Col>
        <p className={status.success === false ? "danger" : "success"}>{status.message}</p>
      </Col>
    )}
  </Row>
</form>
            {error && <p className="danger">{error}</p>}
            {success && <p className="success">{success}</p>}
          </Col>
        </Row>
      </Container>
    </section>
  );
};
