import { faEnvelope, faLocationDot, faPhone } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {Container, Row, Col} from 'react-bootstrap';
import './Kapcsolat.css';
import { GoogleMaps } from './GoogleMaps';
import './GoogleMaps.css';

export const Kapcsolat = () =>  {
    return(
        <section className='kapcsolat' id='kapcsolat'>
            <Container>
                <Row className='justify-content-center'>
                    <Col xs={12} md={4} lg={4} className='d-flex flex-column justify-content-center kap-col'>
                        <FontAwesomeIcon className='ikon' icon={faLocationDot} size='5x'/>
                        <h2>Szkhely</h2>
                        <p>1234 Budapest, Ipari t 5.</p>
                    </Col>
                    <Col xs={12} md={4} lg={4} className='d-flex flex-column justify-content-center kap-col'>
                        <FontAwesomeIcon className='ikon' icon={faPhone} size='5x'/>
                        <h2>Telefon</h2>
                        <p>+36 XX XXX XXXX</p>
                    </Col>
                    <Col xs={12} md={4} lg={4} className='d-flex flex-column justify-content-center kap-col'>
                        <FontAwesomeIcon className='ikon' icon={faEnvelope} size='5x'/>
                        <h2>E-Mail</h2>
                        <p>info@tuztertech.hu</p>
                    </Col>
                </Row>
                <Container className='maps'>
                    <GoogleMaps/>
                </Container>
            </Container>
        </section>
    );
}