import React, {useEffect, useState} from 'react';

const Koszontes = () => {
    const [koszontes, setKoszontes] = useState("dvzljk az oldalunkon!");
  
  
    useEffect(() => {
      const most = new Date();
      const orak = most.getHours();
  
  
      if (orak < 12) {
        setKoszontes("J reggelt! dvzljk az oldalunkon!");
      }
      else if(orak < 18){
        setKoszontes("J napot! dvzljk az oldalunkon!");
      }
      else {
        setKoszontes("J estt! dvzljk az oldalunkon!");
      }
    }, []);

    return (
        <span id='koszontes' className='tagline'>
            {koszontes}
        </span>
    );
  };

export default Koszontes;