import { Container, Row, Col } from "react-bootstrap";
import kep from '../assets/img/szereles.jpg';
import egeto1 from '../assets/img/egeto1.jpeg';
import egeto2 from '../assets/img/egeto2.jpeg';
import egeto3 from '../assets/img/egeto3.jpeg';
import egeto_utana1 from '../assets/img/egeto_utana1.jpg';
import egeto_utana2 from '../assets/img/egeto_utana2.jpg';
import egeto_utana3 from '../assets/img/egeto_utana3.jpg';
import './problemafelv.css';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleCheck } from '@fortawesome/free-solid-svg-icons';
import tab_pane from './tab_pane';
import ImageSlider from "./tab_pane";


export const Problemafelv = () => {

    const tabs1 = [
        {
          title: '1. kp',
          imageUrl: egeto1
        },
        {
          title: '2. kp',
          imageUrl: egeto2
        },
        {
          title: '3. kp',
          imageUrl: egeto3
        }
      ];

      const tabs2 = [
        {
          title: '1. kp',
          imageUrl: egeto_utana2
        },
        {
          title: '2. kp',
          imageUrl: egeto_utana3
        }
      ];

    return(
        <section className='problema' id='problema'>
            <Container>
                <Row className='align-items-center'>
                    <Col  xs={12} md={6} xl={6}>
                    <div className="sliderdiv" style={{ maxWidth: '600px', margin: '0 auto', height: '500px' }}>
                        <h2>Eltte: </h2>
                        <ImageSlider slides={tabs1}/>
                        <h2>Utna: </h2>
                        <ImageSlider slides={tabs2}/>
                    </div>
                    </Col>
                    <Col xs={12} md={6} xl={6}>
                        <h1>
                        Mirt fontos a megbzhat incinertor?
                        </h1>
                        <p>
                        Egy getberendezs meghibsodsa nem csupn anyagi vagy technikai gond: veszlyeztetheti az egsz telep biztonsgt, a termelst, s akr krnyezetvdelmi kihgsokat is okozhat. A telephelyen felgyl veszlyes hulladk, llati tetemek vagy egyb megsemmistend anyagok komoly kockzatot jelentenek, ha az get nem zemel megfelelen.
                        </p>
                        <h2>
                        Milyen kockzatok vannak hiba esetn?
                        </h2>
                        <ul>
                            <li>
                                <div className='top-content'>                            
                                    <FontAwesomeIcon size="2x" className="ikon" icon={faCircleCheck} />
                                    <h3>Fertzsveszly s egszsggyi kockzatok </h3>
                                </div>
                                    <p>Ha az llati tetemek, hulladkok elszlltsra vrnak, nvekszik a fertzsek terjedsnek eslye.</p>  
                            </li>
                            <li>
                                <div className='top-content'>                            
                                    <FontAwesomeIcon size="2x" className="ikon" icon={faCircleCheck} />
                                    <h3>zemsznet s termelskiess  </h3>
                                </div>
                                    <p>Egy vratlan lells megakaszthatja a teljes telep mkdst, ami slyos anyagi krokat okozhat.</p>  
                            </li>
                            <li>
                                <div className='top-content'>                            
                                    <FontAwesomeIcon size="2x" className="ikon" icon={faCircleCheck} />
                                    <h3>Folyamatos kiadsok</h3>
                                </div>
                                    <p>Az get meghibsodsakor sokszor drga szlltsi, elszllttatsi s javtsi kltsgekkel is szmolni kell.</p>  
                            </li>
                        </ul>
                    </Col>
                    
                </Row>
                <h2>A gyors s hatkony megolds: a TzTrTech szervzcsapata</h2>
                <p>Mi <span className='highlight'>egyedi, helyszni javtsi technolgiinkkal</span>, tbb vtizedes tapasztalatunkkal s gyors reaglsunkkal biztostjuk, hogy berendezsei minl kevesebb idt lljanak. Nlunk nem kell hosszadalmas beszlltsra vagy kln szervizllomsokra vrnia: mindent igyeksznk <span className="highlight">ott s akkor megoldani</span>, amikor nnek a legnagyobb szksge van r.</p>
            </Container>
        </section>
    )
}