import { Container, Row, Col } from "react-bootstrap";
import logo from "../assets/img/trans_logo.png";
import navIcon1 from "../assets/img/kalyhasok.png";
import './Footer.css';

export const Footer = () => {
  return (
    <footer className="footer">
      <Container>
        <Row className="align-items-center">
          
          <Col size={12} xs={12} sm={6}>
          <div className="kuldes">
            <img src={logo} alt="Logo" />
            </div>
          </Col>
          <Col size={12} xs={12} sm={6} className="text-center text-sm-end">
            <div className="social-icon">
              <a href="https://www.kalyhasok.hu/"><img src={navIcon1} alt="Icon" /></a>
            </div>
            <p className='copyright'> TzTrtech 2025.</p>
          </Col>
        </Row>
      </Container>
    </footer>
  )
}
