import './Elofizetes.css';
import {Row, Col, Container} from 'react-bootstrap';

export const Elofizetes = () => {
    return(
        <section className='elofizetes'>
            <Container>
                <br></br>
                <h1>Elfizetses konstrukcik</h1>
                <Row>
                    <Col xs={6} md={4} lg={4}>
                        <Container className='kartya'>
                            <h2>24-72 rs kiszlls </h2>
                            <p>Havi fix 20 000 Ft-os dj ellenben elfizetinknl garantljuk, hogy hiba esetn legfeljebb 72 rn bell, de sokszor mr 24 rn bell is megjelennk.</p>
                        </Container>
                    </Col>
                    <Col xs={6} md={4} lg={4}>
                        <Container className='kartya'>
                            <h2>ves 300 000 Ft-os javtsi csomag</h2>
                            <p>Az elfizets rszeknt vente 300 ezer forintig djmentesen vgezzk el az esetlegesen szksges javtsokat.</p>
                        </Container>
                    </Col>
                    <Col xs={12} md={4} lg={4}>
                        <Container className='kartya'>
                            <h2>Elnyt lvez a tbbi gyfllel szemben</h2>
                            <p>Az elfizet partnereink kiszolglsa mindig elsbbsget lvez, gy akr htvgn vagy nnepnapokon is szmthatnak rnk.</p>
                        </Container>
                    </Col>
                </Row>
            </Container>
        </section>
    )
}