import React, { useState } from 'react';
import { Button } from './Button';
import { Link } from 'react-scroll';
import './NavBar.css';
import Dropdown from './Dropdown';
import logo from '../assets/img/trans_logo.png';
import classNames from 'classnames';

export const NavBar2 = () => {
  const [click, setClick] = useState(false);
  const [dropdown, setDropdown] = useState(false);

  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);

  const onMouseEnter = () => {
    if (window.innerWidth < 960) {
      setDropdown(false);
    } else {
      setDropdown(true);
    }
  };

  const onMouseLeave = () => {
    if (window.innerWidth < 960) {
      setDropdown(false);
    } else {
      setDropdown(false);
    }
  };

  return (
    <>
      <nav className='navbar'>
      <img src={logo} alt='logo' className='nav-logo'/>
        <Link to='home' smooth={true} duration={500} className='navbar-logo' onClick={closeMobileMenu}>
          TzTrTech
        </Link>
      
        <div className='menu-icon' onClick={handleClick}>
          <i className={click ? 'fas fa-times' : 'fas fa-bars'} />
        </div>
        <ul className={click ? 'nav-menu active' : 'nav-menu'}>
          <li className='nav-item'>
            <Link to='home' smooth={true} duration={500} className='nav-links' onClick={closeMobileMenu}>
              Foldal
            </Link>
          </li>
          <li
            className={classNames('nav-item', 'eltuno')}
            onMouseEnter={onMouseEnter}
            onMouseLeave={onMouseLeave}
          >
            <Link
              to='problema' smooth={true} duration={500}
              className='nav-links'
              onClick={closeMobileMenu}
            >
              Problmafelvets <i className='fas fa-caret-down'/>
            </Link>
            {dropdown && <Dropdown />}
          </li>
          <li className='nav-item'>
            <Link
              to='szolgaltatasaink' smooth={true} duration={500}
              className='nav-links'
              onClick={closeMobileMenu}
            >
              Szolgltatsaink
            </Link>
          </li>
          <li className={classNames('nav-item','eltuno')}>
            <Link
              to='miertmi' smooth={true} duration={500}
              className='nav-links'
              onClick={closeMobileMenu}
            >
              Mirt mi?
            </Link>
          </li>
          <li className={'nav-item'}>
            <Link
              to='referenciak' smooth={true} duration={500}
              className='nav-links'
              onClick={closeMobileMenu}
            >
              Referencik
            </Link>
          </li>
          <li className={classNames('nav-item', 'eltuno')}>
            <Link
              to='bemutatkozas' smooth={true} duration={500}
              className='nav-links'
              onClick={closeMobileMenu}
            >
              Bemutatkozs
            </Link>
          </li>
          <li className='nav-item'>
            <Link
              to='kapcsolat' smooth={true} duration={500}
              className='nav-links'
              onClick={closeMobileMenu}
            >
              Kapcsolat
            </Link>
          </li>
          <li>
            <Link
              to='hibabejelentes' smooth={true} duration={500}
              className='nav-links-mobile'
              onClick={closeMobileMenu}
            >
              Hibabejelents
            </Link>
          </li>
        </ul>
        <Button />
      </nav>
    </>
  );
}

