import { useCallback, useState, useEffect } from "react";
import { useDropzone } from "react-dropzone";
import './fajltoltesform.css';

export const FajlFejltolt = ({ onImageChange }) => {  
    const [images, setImages] = useState([]);  
    
    // Renderels utn parent comp triggerels
    useEffect(() => {
      if (images.length > 0) {  
        onImageChange(images);  
      }
    }, [images, onImageChange]);  


    

    const onDrop = useCallback((acceptedFiles) => {
      const newImages = acceptedFiles.map((file) => ({
        id: URL.createObjectURL(file),
        file,
      }));

      setImages((prevImages) => [...prevImages, ...newImages]);
    }, []);

    const removeImage = (id) => {
      const updatedImages = images.filter((image) => image.id !== id);
      setImages(updatedImages);
    };

    const { getRootProps, getInputProps } = useDropzone({
      onDrop,
      accept: "image/*",
      multiple: true,
    });

    return (
      <div className="feltolt" style={{ textAlign: "center" }}>
        <div
          {...getRootProps()}
          style={{
            border: "2px dashed #ccc",
            padding: 20,
            cursor: "pointer",
            marginBottom: 10,
          }}
        >
          <input {...getInputProps()} />
          <p className="feltolt_p">Ide hzza a hibrl a kpeit, vagy kattintson ide, hogy kivlaszthassa ket. </p>
        </div>

        <div style={{ display: "flex", gap: 10, flexWrap: "wrap", justifyContent: "center" }}>
          {images.map((image) => (
            <div key={image.id} style={{ position: "relative", display: "inline-block" }}>
              <img src={image.id} alt="Preview" width="100" style={{ borderRadius: "5px" }} />

              <button className="torles"
                onClick={() => removeImage(image.id)}
                style={{
                  position: "absolute",
                  top: 5,
                  right: 5,
                  background: "#A60000",
                  color: "white",
                  border: "none",
                  borderRadius: "50%",
                  width: "20px",
                  height: "20px",
                  cursor: "pointer",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  fontSize: "12px",
                }}
              >
                
              </button>
            </div>
          ))}
        </div>
      </div>
    );
};