import './Referenciak.css';
import {Container} from 'react-bootstrap';

export const Referenciak = () => {
    return(
        <section className='referenciak' id='referenciak'>
            <Container className='referenciak-container'>
                <h2>Hogyan mentettnk meg egy <br/> <span className='fejlec'>kipposodott tzteret htvgn?</span></h2>
                <Container className='card'>
                    <p>Nemrg egy nagy llattart telephez riasztottak minket, ahol ismeretlen okbl a tztr fala tbb helyen kipposodott, s furcsa gsnyomok jelentek meg. Elszr azt gondolhattk, hogy a szigetels nem brta a ht, de a helysznen kszlt fotkat elemezve kiderlt: az gfej elmozdult, ami flrefjta a lngot, gy a szigetels kezdett tnkremenni.
                    A telepre szigor belptetsi szablyok miatt csak elre egyeztetett idpontban lehetett bejutni, ezrt azonnal telefonltunk az zemvezetnek, aki mg htvgn is ksz volt minket fogadni. Nhny rs munkval sikerlt stabilizlni a tzteret, s a nagyobb cserk elvgzsig is biztonsgosan hasznlhatv tettk a berendezst. Ezltal a telep elkerlte a termelskiesst s jelents vesztesgeket.
                    </p>
                    <p className='idezet'>A Tztr Tech csapata gyorsan reaglt s a helysznen oldotta meg a problmt. Msklnben le kellett volna lltanunk az egsz zemet.</p>
                    <p className='nev'> llattart telep vezetje</p>
                </Container>
            </Container>
        </section>
    )
}