import './karbantartas.css';
import { Container, Row, Col } from "react-bootstrap";
import { faEnvelopeOpenText, faHammer } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export const Karbantartas = () => {
    return(
        <section className="karbantartas" id="karbantartas">
            <Container>
                <h1>Rendszeres Karbantarts</h1>
                <Row className='align-items-center'>
                    <Col xs={12} md={4} lg={4}>
                        <h2 className='kar-h2'>Negyedves, havi vagy egyedi temezs</h2>
                        <p>gyfeleinkkel kzsen alaktjuk ki a karbantarts gyakorisgt, hogy az mindig megfeleljen az get hasznlati szoksainak s terhelsnek.</p>
                    </Col>
                    
                    <Col xs={6} md={2} lg={2}>
                        <FontAwesomeIcon className="ikon elso" icon={faHammer} size="5x"/>
                    </Col>
                    <Col xs={6} md={2} lg={2}>
                         <FontAwesomeIcon className="ikon masodik" icon={faEnvelopeOpenText} size="5x"/>
                    </Col>
                    <Col xs={12} md={4} lg={4}>
                        <h2 className='kar-h2'>Apr hibk elhrtsa</h2>
                        <p>A karbantartsok sorn szlelt kisebb repedsek, szigetelsi gondok vagy elmozdult alkatrszek javtsval megelzzk a ksbbi komolyabb meghibsodsokat.</p>
                    </Col>
                </Row>
            </Container>
        </section>
    )
}